body {
  width: 350px;
  margin: 0 auto;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  margin: 0 auto;
  height: -webkit-fill-available;
  align-content: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

main {
  overflow: auto;
  height: -webkit-fill-available;
}

ul {
  display: flex;
  flex-direction: column-reverse;
  list-style: none;
  padding: 5px;
}

button {
  background-color: white;
  color: black;
  border: 2px solid rgb(13, 141, 154);
  border-radius: 12px;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
}

button:hover {
  background-color: rgb(13, 141, 154);
  color: white;
  cursor: pointer;
}

:active, :hover, :focus {
  outline: 0;
  outline-offset: 0;
}

li {
    display: flex;
    font-size: 22px;
    border-bottom: aliceblue 1px solid;
}

.active {
    color: darkblue;
}

.archive > span {
    color: gray;
    text-decoration: line-through;
}

.isHidden {
    display: none;
}

.taskBtn {
    background: #ffffff;
    border-radius: 6px;
    margin: 1px;
    font-size: 20px;
    border: none;
}

.taskBtn:hover {
    background-color: rgb(13, 141, 154);
}

.taskTitle {
    display: block;
    width: 240px;
    overflow-wrap: break-word;
    overflow: hidden;
}

.checkContainer {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  
  .checkContainer input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }
  
  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
  }
  
  .checkContainer:hover input ~ .checkmark {
    background-color: #ccc;
  }
  
  .checkContainer input:checked ~ .checkmark {
    background-color: #2196F3;
  }
  
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  .checkContainer input:checked ~ .checkmark:after {
    display: block;
  }
  
  .checkContainer .checkmark:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
  }
footer {
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: white;
    min-height: 50px;
    padding: 15px 0;
    overflow: auto;
    display: flex;
    justify-content: space-around;
}

.logo {
    width: 65px;
    float: left;
    padding: 10px;
}

header > h1 {
    margin-bottom: -11px;
    top: -15px;
    position: relative;
}

.control > button {
  height: 25px;
  width: 85px;
  margin-right: 3px;
  border: 1px solid rgb(13, 141, 154);
  background-color: white;
  color: darkblue;
  font-size: 16px;
}

.control > button:hover {
    background-color: rgb(13, 141, 154);
    color: white;
    cursor: pointer;
}

  .taskInput {
    width: 97%;
    height: 35px;
    border: none;
    border-bottom: 2px solid red;
    padding-left: 10px;
    font-size: 20px;
    outline: none;
  }
  
  .taskInput:focus {
    border-bottom: 2px solid #555;
  }
  
  .taskInputLabel {
    position: absolute;
    top: 0;
    left: 20%;
    font-size: var(--font-size-small);
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
  }
  
  .taskInput:placeholder-shown + label {
    opacity: 0;
    -webkit-transform: translateY(1rem);
            transform: translateY(1rem);
  }
  
